import React from "react";

import './Menu.scss'

export default class Menu extends React.Component {

    render() {
        const { isMobile, isFooter, menuItems } = this.props;

        const menuItemsJSX = menuItems.map((menuItem) => {
            return (
                <li className={`menu__item${isMobile ? ' mobile' : ''}${isFooter ? ' footer' : ''}`}><a href="/">{menuItem}</a></li>
            )
        });
        return (
            <nav className={`menu${isMobile ? ' mobile' : ''}`}>
                <ul className={`menu__items${isMobile ? ' mobile' : ''}${isFooter ? ' footer' : ''}`}>
                    {menuItemsJSX}
                </ul>
            </nav>
        )
    }
}