import React from "react";

import './ExtendedMenu.scss';

export default class ExtendedMenu extends React.Component {
    menuItems = [
        'News', 'Overview', 'Tutorials', 'FAQ',
        'Blog', 'Design', 'Resources', 'Terms',
        'Partners', 'Code', 'Guides', 'Conditions',
        'Shop', 'Collaborate', 'Examples', 'Help'
    ];

    render() {
        const menuItemsJSX = this.menuItems.map((menuItem) => {
            return (
                <li className="extended-menu__item"><a href="/">{menuItem}</a></li>
            )
        });
        return (
            <nav className="extended-menu">
                <ul className="extended-menu__list">
                    {menuItemsJSX}
                </ul>
            </nav>
        )
    }
}