import React from "react";

import './WelcomeBlock.scss';
import '../../../../Shared/Scss/typography.scss';
import '../../../../Shared/Scss/buttons.scss';
import Button from "@material-ui/core/Button";

import '../../../../Shared/images/bg/plain/2k.jpg';
import '../../../../Shared/images/bg/plain/desktop.jpg';
import '../../../../Shared/images/bg/plain/tablet.jpg';
import '../../../../Shared/images/bg/plain/mobile.jpg';

import '../../../../Shared/images/bg/retina/2k.jpg';
import '../../../../Shared/images/bg/retina/desktop.jpg';
import '../../../../Shared/images/bg/retina/tablet.jpg';
import '../../../../Shared/images/bg/retina/mobile.jpg';

export default class WelcomeBlock extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            bannerContentText: '',
        };

        const maxMobileSize = 744;
        const plainText = `
            We kindly remind you that your test assignment should
            be submitted as a link to github/bitbucket repository.
            Please be patient, we consider and respond to every application that meets minimum requirements. 
            We look forward to your submission. Good luck!
        `;
        const shortText = `
            We kindly remind you that your test assignment should be submitted as a link to github/bitbucket repository. 
        `

        document.addEventListener("DOMContentLoaded", () => {
            if (document.body.clientWidth > maxMobileSize) {
                this.setState({
                    bannerContentText: plainText,
                });
                return;
            };

            this.setState({
                bannerContentText: shortText,
            });
        });
    }

    render() {

        return (
            <section className="welcome-block">
               <div className="welcome-block__container">
                   <div className="welcome-block__content">
                       <h1 className="heading1">Test assignment for Frontend Developer position</h1>
                       <p className="welcome-block__content-text">{this.state.bannerContentText}</p>
                       <div className="welcome-block__content-button button">
                           <Button variant="contained" className="button-item button-item_primary">
                               Sign Up
                           </Button>
                       </div>
                   </div>
               </div>
            </section>
        )
    }
}